@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
:root {
  font-family: 'Inter', sans-serif;
  height: 100%;
  --max-width: 1100px;
  --border-radius: 12px;

  --accent-rgb: 3, 255, 189;
  --on-accent-rgb: 0, 0, 0;

  --foreground-rgb: 255, 255, 255;
  --secondary-foreground-rgb: 143, 143, 143;

  --background-rgb: 18, 18, 18;
  --secondary-background-rgb: 27, 27, 27;
  --tertiary-background-rgb: 42, 42, 42;
}

html {
  height: 100%;


}


* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html,
body {

  max-width: 100vw;
  max-height: 100vh;
  overflow-x: hidden;

}



a {
  color: inherit;
  text-decoration: none;
}


button.wallet-adapter-button-trigger {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: rgb(255, 255, 255) !important;
  color: rgb(var(--foreground-rgb)) !important;
  border: 1px solid rgb(42, 42, 42) !important;
  border: 1px solid rgb(var(--tertiary-background-rgb)) !important;
  border-radius: 12px !important;
  border-radius: var(--border-radius) !important;
  background-color: #683EB5 !important;
  width: 100% !important;
  border-radius: 8px !important;
}

button.wallet-adapter-button-trigger:hover {
  background-color: rgb(var(--hover-background-rgb)) !important;
}

.wallet-adapter-dropdown {
  display: flex !important;
  width: 100% !important;
}


.timer-details {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #1b174f;
  padding: 4px;
  border-radius: 4px;
  margin: 0px 2px;
  max-width: 24px;
}

.chakra-button {
  white-space: unset !important;
  min-width: -webkit-fit-content !important;
  min-width: -moz-fit-content !important;
  min-width: fit-content !important;
}
.App {
